import styled from "styled-components"

export const Layout = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  .main-content {
    position: relative;
    width: 100%;
    min-height: 100vh;
    @media print {
      margin-top: 0 !important;
    }
  }
`
