import styled from "styled-components"

export const ElementSection = styled.div`
  margin-top: 24px;
  & .section-title {
    color: ${({ theme }) => theme.gray_01};
    text-transform: uppercase;
  }
  & .section-caption {
    color: ${({ theme }) => theme.gray_01};
  }
`

export const ElementList = styled.div`
  background-color: ${({ theme }) => theme.background_primary};
  & a {
    color: ${({ theme }) => theme.text_primary};
    &:hover {
      color: ${({ theme }) => theme.text_primary};
    }
  }
  div:not(:last-child):after {
    position: absolute;
    content: "";
    display: flex;
    width: 100%;
    height: 1px;
    background-color: ${({ theme }) => theme.separator_non_opaque};
    bottom: 0;
    margin-left: 32px;
    cursor: pointer;
  }
`
