import styled from "styled-components"

export const MainNav = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1030;
  backdrop-filter: saturate(180%) blur(24px);
  background-color: ${({ theme }) => theme.background_primary_blur};
  box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  @media print {
    display: none;
  }
  nav {
    font-size: 17px;
    line-height: 24px;
    display: flex;
    min-height: 48px;
    .main-nav-left {
      display: flex;
      height: 48px;
      color: ${({ theme }) => theme.gray_01_reverse};
      align-items: center;
      text-decoration: none;
      white-space: nowrap;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      :hover {
        color: ${({ theme }) => theme.text_primary};
      }
      :hover svg {
        fill: ${({ theme }) => theme.text_primary};
      }
      svg {
        fill: ${({ theme }) => theme.gray_01_reverse};
        margin-right: 12px;
        transition: fill 0.15s ease-in-out;
      }
    }
    .main-nav-center {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 48px;
      max-width: 1200px;
      margin: 0 48px;
      user-select: none;
      a {
        display: flex;
        align-items: center;
        height: 100%;
        color: ${({ theme }) => theme.gray_01_reverse};
        text-decoration: none;
        margin: 0 8px;
        transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
          border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
        :hover {
          color: ${({ theme }) => theme.text_primary};
        }
      }
      @media (max-width: 1200px) {
        display: none;
      }
    }
    .main-nav-right {
      display: flex;
      height: 48px;
      color: ${({ theme }) => theme.gray_01_reverse};
      align-items: center;
      cursor: pointer;

      i {
        display: flex;
        width: 24px;
        height: 24px;
        justify-content: center;
        align-items: center;
        transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
          border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
        :hover {
          color: ${({ theme }) => theme.text_primary};
        }
        :not(:first-child) {
          margin-left: 16px;
        }
        div {
          position: absolute;
          cursor: pointer;
          width: 36px;
          height: 36px;
        }
      }
      @media (max-width: 1200px) {
        display: none;
      }
    }
    @media (max-width: 1200px) {
      justify-content: center;
    }
  }
`
