import { styleVariables } from "projectSettings"
import styled from "styled-components"

export const Row = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  overflow: hidden;
  width: 100%;
  color: ${({ theme }) => theme.text_primary};
  & i {
    display: flex;
    align-items: center;
    justify-content: center;
    &.primary {
      color: ${({ theme }) => theme.tint_blue};
    }
    & div {
      position: absolute;
      cursor: pointer;
    }
  }
  & .left-part {
    position: absolute;
    left: 0;
    display: flex;
    align-items: center;
    height: 100%;
  }
  & a.left-part {
    width: 100%;
    text-decoration: none;
    color: ${({ theme }) => theme.text_primary};
  }
  & input.left-part {
    width: 100%;
    color: ${({ theme }) => theme.text_primary};
    background-color: ${({ theme }) => theme.fill_primary};
    background-clip: padding-box;
    border: none;
    border-radius: ${styleVariables.root.primaryBorderRadius};
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    &::placeholder {
      color: ${({ theme }) => theme.text_tertiary};
    }
    &:invalid {
      box-shadow: none;
    }
  }
  & .center-part {
    position: absolute;
    display: flex;
    align-items: center;
    height: 100%;
    color: ${({ theme }) => theme.text_primary};
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    &:hover {
      color: ${({ theme }) => theme.text_primary};
      text-decoration: none;
    }
  }
  & .right-part {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    height: 100%;
  }
`
