import React from "react"
import * as S from "globalOrganisms/MainNav/styles"
import { DropletHalf, Printer } from "react-bootstrap-icons"
import { window, document } from "browser-monads"

export default function MainNav() {
  return (
    <S.MainNav>
      <nav className="container-fluid" style={{ maxWidth: "992px" }}>
        <a href="/" className="main-nav-left">
          <svg
            width="24"
            height="24"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M13.3603 2L20.7919 14.8719L16.939 22L13.6065 16.2281L15.4747 12.7715L11.7748 13.0555L9.50735 9.12808L13.3603 2ZM11.9624 19.2519L7.98122 11.953L4 19.2519H11.9624Z"
            />
          </svg>
          <span>Artem Attvud</span>
        </a>
        <div className="main-nav-center large-subhead">
          <a href="/resume">Resume</a>
          <a href="https://www.linkedin.com/in/attvud" target="blank">
            Recommendations
          </a>
          <a href="/portfolio">Portfolio</a>
          <a href="/contacts">Contacts</a>
        </div>
        <div className="main-nav-right">
          {/* <i>
            <div id="theme-toggler"></div>
            <DropletHalf size="20"></DropletHalf>
          </i> */}
          <i>
            <div onClick={() => window.print()}></div>
            <Printer size="20"></Printer>
          </i>
        </div>
      </nav>
    </S.MainNav>
  )
}
