import React from "react"
import * as S from "./styles"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"

export default function Layout(props) {
  return (
    <S.Layout as="main" className={props.className} id={props.id}>
      <Sidebar />
      <MainNav></MainNav>
      <div className="main-content mt-48 mb-56">{props.children}</div>
    </S.Layout>
  )
}
