import styled from "styled-components"

export const SideBar = styled.aside`
  display: none;
  flex-direction: column;
  width: 100%;
  max-width: ${props => props.styleProps.maxWidth};
  z-index: 1050;
  @media (max-width: 1200px) {
    display: flex;
    left: 0;
    position: fixed;
  }

  footer {
    position: fixed;
    bottom: 0;
    display: flex;
    width: 100%;
    height: 56px;
    max-width: ${props => props.styleProps.maxWidth};
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque};
    visibility: hidden;
    .footer-button {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: ${({ theme }) => theme.text_secondary};
      & :hover {
        color: ${({ theme }) => theme.tint_blue} !important;
      }
      i {
        display: flex;
        width: 24px;
        height: 24px;
        justify-content: center;
        align-items: center;
      }
    }
    @media (max-width: 1200px) {
      max-width: 100%;
      visibility: visible;
    }
  }
  .sidebar-content {
    position: fixed;
    width: 100%;
    max-width: ${props => props.styleProps.maxWidth};
    height: 100%;
    top: 0;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    background-color: ${({ theme }) => theme.background_secondary};
    box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
    &::-webkit-scrollbar {
      display: none;
    }
    header {
      z-index: 1030;
      position: fixed;
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: ${props => props.styleProps.maxWidth};
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
        1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
      top: 0;
      @media (max-width: 576px) {
        max-width: 100%;
      }
    }
    @media (max-width: 1200px) {
      visibility: hidden;
    }
    @media (max-width: 576px) {
      max-width: 100%;
    }
  }
`
